body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(247, 247, 247);
  white-space: pre-wrap;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper {
  width: 100%;
  overflow: hidden;
  min-height: 10000px;
  transition: all, cubic-bezier(0.165, 0.84, 0.44, 1)
}

.wrapper div,
.wrapper p {
  position: relative;
  z-index: 2;
}

.item {
  position: absolute;
  top: 1600px;
  right: 0;
  opacity: 0.7;
  z-index: 1;
}

.item_left {
  left: 100px;
  right: auto;
  width: 200px;
  opacity: 0.8;
}

.bg {
  width: 1000px;
  margin: 0 auto;
  display: flex;
  height: 400px;
  justify-content: center;
  align-items: center;
}

.bg div {
  display: inline-block;
  background: rgba(255, 255, 255, 0.5);
  text-align: center;
  padding: 40px 120px;
  font-size: 50px;
}

.bg1 {
  background: url(./images/bg1.webp) no-repeat;
}

.bg2 {
  margin-top: 50px;
  background: url(./images/bg2.webp) no-repeat;
}

.desc {
  font-weight: bold;
  font-size: 80px;
  color: rgb(47, 47, 47);
  left: 100%;
}

.desc2 {
  font-weight: bold;
  font-size: 60px;
  color: rgb(47, 47, 47);
  right: 100%;
}

.desc3 {
  margin-bottom: 800px;
  font-weight: bold;
  text-align: center;
  font-size: 30px;
  color: rgb(47, 47, 47);
  opacity: 1;
}